@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700&display=swap);
body {
  margin: 0;
  font-family: "Quicksand", sans-serif;
  font-size: 1rem; }

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  min-height: 200px;
  background-color: #cfd8dc; }

#quote-box {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
  width: 50%;
  max-width: 600px;
  height: auto;
  display: flex;
  flex-direction: column;
  padding: 0.8rem;
  background-color: #eceff1;
  color: #263238; }
  #quote-box .quote {
    text-align: center; }
    #quote-box .quote #text {
      font-size: 1.75rem; }
    #quote-box .quote #author {
      font-weight: bold; }
  #quote-box .quoteBox-buttons {
    display: flex;
    justify-content: space-between; }
    #quote-box .quoteBox-buttons .btn {
      border: none;
      outline: none;
      background-color: #263238;
      color: #eceff1;
      padding: 0.6rem;
      cursor: pointer;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      font-size: inherit;
      border-radius: 4px;
      margin-bottom: 10px; }
      #quote-box .quoteBox-buttons .btn:hover {
        background-color: #37474f; }
    #quote-box .quoteBox-buttons a {
      text-decoration: none;
      font-size: inherit; }

@media screen and (max-width: 450px) {
  #quote-box {
    text-align: center;
    max-height: 90vh;
    min-width: 85vw; }
    #quote-box .quote #text {
      font-size: 1.4rem; }
    #quote-box .quoteBox-buttons {
      flex-direction: column; } }

@media screen and (device-width: 320px) and (device-height: 640px) and (-webkit-device-pixel-ratio: 3) and (orientation: landscape) {
  #quote-box {
    max-height: 70vw;
    min-width: 60vw; }
    #quote-box .quote #text {
      font-size: 1.5rem; } }

